import React, { useState } from 'react';
import { Form, Input, Button, message } from 'antd';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const Login = () => {
  const navigate = useNavigate();
  const [errorMsg, setErrorMsg] = useState('');

  const onFinish = (values) => {
    axios.post('/api/login', values)
      .then(res => {
        const msg = res.data.message;
        if (msg === '') {
          message.success(msg);
          setErrorMsg('');
          navigate('/home');
        } else {
          setErrorMsg(msg);
        }
      })
      .catch(err => {
        const msg = err.response?.data?.message || '';
        setErrorMsg(msg);
      });
  };

  return (
    <div style={{ maxWidth: 300, margin: '100px auto' }}>
      <h2 style={{ textAlign: 'center' }}></h2>
      <Form onFinish={onFinish}>
        <Form.Item name="username" rules={[{ required: true, message: '' }]}>
          <Input placeholder="" />
        </Form.Item>
        <Form.Item name="password" rules={[{ required: true, message: '' }]}>
          <Input.Password placeholder="" />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit" block></Button>
          <Button type="link" block onClick={() => navigate('/register')}></Button>
        </Form.Item>
      </Form>
      {errorMsg && (
        <div style={{ color: 'red', textAlign: 'center' }}>{errorMsg}</div>
      )}
    </div>
  );
};

export default Login;
